.gamePin {
  position: absolute;
  margin-bottom: 0;
  top: calc(14.5%);
  font-family: var(--font-poppins);
  font-size: 3vh;
  color: white
}
.mainWrapper {
  position: absolute;
  top: calc(26%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  margin-bottom: 3.5vh;
}
.cardWrapper {
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: var(--font-poppins);
  font-size: 3.5vh;
}
.timerWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  gap:8px;
  margin-top: 1vh;
  margin-bottom: 3vh;
}
.timer {
  border: 0.5vh solid rgb(49, 49, 49);
  border-radius: 15px;
  padding: 0.5vh 2.5vh;
  height: 100%;
  width: 25%;
  cursor: pointer;
  font-family: var(--font-poppins);
  font-size: 2.3vh;
  font-weight: 400;
}
.bottomWrapper {
  position: absolute;
  top: calc(68%);
  border: 0.5vh solid rgba(255, 255, 255, 0.849);
  border-radius: 20px;
  width: 90%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: var(--font-poppins);
}
.playersInGame {
  font-family: var(--font-poppins);
  font-size: 3.7vh;
  color: white;
  width: 100%;
  text-align: center;
}